import React, { useState } from 'react';
import { useHistory } from 'react-router';

import ServiceWrapper from '../components/ServiceWrapper';
import {
  ContentsWrap,
  StyledHeader,
  Box,
  Label,
  InputContainer,
  StyledInput,
  StyledTextArea,
  BottomWrap,
  StyledButton,
  TextButton,
} from '../styles/pages/Support.style';

import { api } from '../utils/api';
import useWindowSize from '../utils/useWindowSize';

const SupportSubmit = () => {
  const history = useHistory();
  const [info, setInfo] = useState({ title: '', contents: '' });
  const { width: screenWidth = 1980 } = useWindowSize();
  const isSmallScreen = screenWidth <= 500;

  const handleSubmit = async () => {
    await api.post('/inquiry/register', { ...info });
    history.goBack();
  };

  const handleChange = (e: any) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  return (
    <ServiceWrapper>
      <ContentsWrap>
        <StyledHeader className="alignedCenter">문의/요청하기</StyledHeader>
        <Box>
          <Label>제목</Label>
          <InputContainer>
            <StyledInput
              name="title"
              placeholder="문의 제목을 입력해주세요."
              value={info.title}
              onChange={handleChange}
            />
          </InputContainer>
          <Label>문의내용</Label>
          <InputContainer>
            <StyledTextArea
              name="contents"
              placeholder="문의하실 내용을 입력해주세요."
              rows={7}
              value={info.contents}
              onChange={handleChange}
            />
          </InputContainer>
        </Box>
        <BottomWrap>
          <StyledButton styleType="blue" onClick={handleSubmit}>
            문의 작성하기
          </StyledButton>
          {isSmallScreen ? (
            <StyledButton
              styleType="outline"
              onClick={() => history.push('/support')}
            >
              취소
            </StyledButton>
          ) : (
            <TextButton to="/support">취소</TextButton>
          )}
        </BottomWrap>
      </ContentsWrap>
    </ServiceWrapper>
  );
};

export default SupportSubmit;
